* {
  margin: 0;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  color: white;
  font-size: 100px;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  display: flex;
  flex-basis: 33%;
  min-width: 300px;
  height: 300px;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-grow: 1;
   
}

.block-1 {
  background-color: rgb(255, 0, 0);
}

.block-2 {
  background-color: rgb(204, 0, 0);
}

.block-3 {
  background-color: rgb(153, 0, 0);
}

.block-4 {
  background-color: rgb(102, 0, 0);
}

.block-5 {
  background-color: rgb(51, 0, 0);
}

.block-6 {
  background-color: rgb(0, 0, 0);
}

@media (min-width:900px) {
  .block-4 {
    order: 3;
  }

  .block-5 {
    order: 2;
  }

  .block-6 {
    order: 1;
  }

}

@media (min-width:600px) and (max-width:899px) {
  .block-3 {
    order: 2;
  }

  .block-4 {
    order: 1;
  }

  .block-5 {
    order: 3;
  }

  .block-6 {
    order: 4;
  }
}
